interface Env {
  VIDEO_STORE: KVNamespace;
  ENVIRONMENT: string;
}

interface Video {
  id: string;
  url: string;
  title: string;
  status: 'pending' | 'processing' | 'completed' | 'failed';
  createdAt: number;
}

export default {
  async fetch(request: Request, env: Env): Promise<Response> {
    const url = new URL(request.url);
    
    // CORS 处理
    if (request.method === 'OPTIONS') {
      return new Response(null, {
        headers: {
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Methods': 'GET, POST, OPTIONS',
          'Access-Control-Allow-Headers': 'Content-Type',
        },
      });
    }

    try {
      // GET /api/videos - 获取视频列表
      if (request.method === 'GET' && url.pathname === '/api/videos') {
        const videosList = await env.VIDEO_STORE.list();
        const videos = await Promise.all(
          videosList.keys.map(async (key) => {
            const videoData = await env.VIDEO_STORE.get(key.name, 'json');
            return videoData;
          })
        );

        return new Response(JSON.stringify({
          success: true,
          data: { videos }
        }), {
          headers: {
            'Content-Type': 'application/json',
            'Access-Control-Allow-Origin': '*',
          },
        });
      }

      // POST /api/videos/parse - 解析新视频
      if (request.method === 'POST' && url.pathname === '/api/videos/parse') {
        const body = await request.json();
        const { url: videoUrl } = body;

        const video: Video = {
          id: crypto.randomUUID(),
          url: videoUrl,
          title: `Video - ${new URL(videoUrl).hostname}`,
          status: 'pending',
          createdAt: Date.now(),
        };

        // 存储视频信息
        await env.VIDEO_STORE.put(video.id, JSON.stringify(video));

        return new Response(JSON.stringify({
          success: true,
          data: video
        }), {
          headers: {
            'Content-Type': 'application/json',
            'Access-Control-Allow-Origin': '*',
          },
        });
      }

      // GET /api/videos/:id - 获取单个视频信息
      if (request.method === 'GET' && url.pathname.startsWith('/api/videos/')) {
        const id = url.pathname.split('/').pop();
        const video = await env.VIDEO_STORE.get(id!, 'json');

        if (!video) {
          return new Response(JSON.stringify({
            success: false,
            error: 'Video not found'
          }), {
            status: 404,
            headers: {
              'Content-Type': 'application/json',
              'Access-Control-Allow-Origin': '*',
            },
          });
        }

        return new Response(JSON.stringify({
          success: true,
          data: video
        }), {
          headers: {
            'Content-Type': 'application/json',
            'Access-Control-Allow-Origin': '*',
          },
        });
      }

      // 处理未知路由
      return new Response(JSON.stringify({
        success: false,
        error: 'Not Found'
      }), {
        status: 404,
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*',
        },
      });

    } catch (error) {
      // 错误处理
      console.error('Worker error:', error);
      
      return new Response(JSON.stringify({
        success: false,
        error: 'Internal Server Error'
      }), {
        status: 500,
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*',
        },
      });
    }
  },
};
